@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
body{
    font-family: 'Poppins';
}
@media(min-width:0px){
.name{
    display: flex;
    align-items: center;
    justify-content: center;
    img{
        width: 80%;
        margin-top: 10px;
        margin-bottom: 10px;
    }
}
.conteiner{
    margin: 0 auto;
width: 100%;
display: flex;
flex-direction: column;
align-items: center;
background-color: rgb(240, 239, 239);

.top{
    box-shadow:  0px 5px 10px 0px rgba(0, 0, 0, 0.5);
    padding: 15px 0;
    margin-top: 20px;
    width: 85%;
    background-color: white;
    border-radius: 8px;
    text-align: center;
    display: flex;
    flex-direction: column;
    h1{
        font-size: 24px;
        text-align: center;
    }
    span{
      font-size: 16px;

    }
}
.main{
    box-shadow:  0px 5px 10px 0px rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    margin-top: 20px;
    width: 85%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    .summa{
        width: 90%;
        display: flex;
        padding: 5px 5px;
        border-bottom: 1px solid rgb(235, 231, 231);
        align-items: center;
        justify-content: space-between;
        p{
           margin: 5px 0;
            color: rgb(184, 181, 181) ;
        }
    }
    
}
.bottom{
    box-shadow:  0px 5px 10px 0px rgba(0, 0, 0, 0.5);
    padding: 15px 0;
    margin-top: 20px;
    width: 85%;
    background-color: white;
    border-radius: 8px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;

    .custom-select {
        position: relative;
        width: 150px;
        padding: 1.5rem;
        margin-right: auto;
        margin-left: auto;
        font-family: sans-serif;
        color: white;
        background-color: #339969;
        cursor: pointer;
      }
      
      .custom-select__icon {
        height: 1rem;
        width: 1rem; 
        vertical-align: middle;
        
        img {
          height: 1rem;
          width: 1rem;
        }
      }
      
      .custom-select__label {
        margin-left: -44px;
        vertical-align: middle;
        
        &:before,
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          width: 10px;
          height: 2px;
          background-color: white;
        }
        
        &:before {
          right: 1.5em;
          transform: translate(-25%, -50%) rotate(45deg)
        }
        
        &:after {
          right: 1.5em;
          transform: translate(25%, -50%) rotate(-45deg); 
        }
      }
      
      .custom-select__option-container {
        position: absolute;
        top: 100%;
        left: 0;
        margin-top: 10px;
        width: 100%;
        height: auto;
        background-color: white;
        transform: rotateY(180deg);
        transition: transform 295ms ease-in-out;
        backface-visibility: hidden;
        
        &.is-open {
          transform: rotateY(360deg);
        }
      }
      
      .custom-select__inner {
        position: relative;
          
        &:before {
          content: '';
          position: absolute;
          bottom: 100%;
          left: 50%;
          width: 0;
          height: 0;
          border-left: 20px solid transparent;
          border-right: 20px solid transparent;
          border-bottom: 20px solid white;
          transform: translateX(-50%);
        }
      }
      
      .custom-select__option {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 1.2em;
        color: lighten(#383b45, 30);
        
        &:after {
          content: '';
          position: absolute;
          top: 100%;
          right: 20px;
          left: 20px;
          height: 1px;
          background-color: lighten(#383b45, 50);
        }
        
        &:last-child:after {
          display: none;
        }
      }
      
      input[type=checkbox] {
        appearance: none;
        position: relative;
        width: 20px;
        height: 20px;
        border: 1px solid lighten(#383b45, 50);
      }
      
      input[type=checkbox]:checked {
        
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          width: 13px;
          height: 7px;
          border: none;
          border-bottom: 3px solid lighten(#383b45, 50);
          border-left: 3px solid lighten(#383b45, 50);
          transform: translate(-50%, -75%) rotate(-45deg);
        }
      }
    input{
        border-radius: 8px;
        padding: 10px 0;
        margin-top: 10px;
        margin-bottom: 15px;
        width: 90%;
        border-color: rgb(209, 209, 209);
    }
    .try{
        padding: 15px 40px;
        border-radius: 8px;
        border: none;
        background-color: rgb(6, 70, 6);
        margin-bottom: 15px;
        color: azure;
        
    }
    .reset{
        padding: 15px 40px;
        border-radius: 8px;
        border: none;
        background-color: rgb(230, 101, 78);
        margin-bottom: 15px;
        color: azure;
        
    }
}
}
}